header {
    line-height: 1.2;
    height: 128px;

    display: flex;
    /* outline: 1px solid black; */
}

@media only screen and (max-width: 700px) {
    header {
        height: auto;
        /* outline: 1px solid black; */
    }
    /* .header-img > img {
        width: 100%;
        height: auto;
        object-fit: cover;
    } */
}

/* Text Side */
.header-txt {
    flex: 1;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.name {
    font-size: 2.5rem;
}

.sub-titles {
    font-size: 0.8rem;
    font-style: italic;
}

.info {
    font-size: 0.9rem;
}

